as ha permitido la innovación y evolución de los procesos, hoy en día los equipos y dispositivos empleados en la medicina, las telecomunicaciones e incluso la educación cuentan con al menos un subsistema diseñado a partir de circuitos electrónicos. Basta simplemente con ver el celular o la televisión para darse cuenta de este hecho.")]),_c('p',{staticClass:"mt-3"},[_vm._v("Los planos esquemáticos que representan cada uno de los circuitos empleados para la fabricación de un producto electrónico se clasifican según el área o ciencia en el que se utilice. Es decir que un equipo biomédico tendrá un plano o planos esquemáticos que sirvan para analizar su electrónica; de igual manera para los equipos de audio y video o para los equipos de telecomunicaciones. Esta clasificación de los planos esquemáticos por áreas o ciencias, facilitan su estudio y permiten que la comprensión técnica y la capacitación del personal que los repara se especialice por la rama o ciencia que le apliquen.")])]),_c('div',{staticClass:"tarjeta color-acento-botones--borde p-4",attrs:{"titulo":"Características"}},[_c('h4',[_vm._v("Características")]),_c('p',[_vm._v("Los planos esquemáticos se caracterizan por ser la antesala a la elaboración del circuito impreso en las fases de diseño electrónico; la información presentada de manera gráfica permite conocer al detalle el funcionamiento del circuito físico de cualquier producto electrónico. En procesos de diagnóstico y reparación de circuitos son muy útiles porque permiten establecer puntos de verificación y validación de funcionamiento, así mismo establecen puntos para la medición de variables y parámetros eléctricos.")]),_c('p',{staticClass:"mt-5 p-1",staticStyle:{"padding-left":"13px !important","border-left":"4px #B3009D solid"},attrs:{"data-aos":"fade-up"}},[_c('strong',[_vm._v("Figura 9.")]),_vm._v(" "),_c('em',[_vm._v("Plano esquemático de un circuito para el control de motores")])]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/template